import React from 'react';
import classnames from 'classnames';
import style from './Select.css';

const Option = props => (
  <li
    unselectable="unselectable"
    onMouseDown={() => {
      props.onChange(props.value);
    }}
    id={props.value === props.selectedValue ? props.id : ''}
    className={classnames(style['dropdown-menu-item'], props.value === props.selectedValue && style['dropdown-menu-item-selected'], props.className)}
    role="menuitem"
  >
    {props.text}
  </li>
);

Option.propTypes = {
  className: React.PropTypes.string,
  onChange: React.PropTypes.func,
  value: React.PropTypes.oneOfType([React.PropTypes.number, React.PropTypes.string]).isRequired,
  selectedValue: React.PropTypes.oneOfType([React.PropTypes.number, React.PropTypes.string]).isRequired,
  text: React.PropTypes.string.isRequired,
};

export default Option;



