import React from 'react';
import * as _ from 'lodash';
import Modal from '../Modal';
import style from './CustomFilter.css';
import Button from '../../Button/Button';
import Link from '../../Link/Link';
import FieldCondition from './FieldCondition';
import * as filterFieldType from '../../../constants/enums/filterFieldType';
import * as customVariableType from '../../../constants/enums/customVariableType';
import * as filterOperatorType from '../../../constants/enums/filterOperatorType';
import * as visitorStatus from '../../../constants/enums/visitorStatus';
import * as visitorStatusText from '../../../constants/languages/visitorStatus';
import * as filterFieldTypeText from '../../../constants/languages/filterFieldType';

const allStatus = [
  { text: visitorStatusText.transferring, value: visitorStatus.transferring },
  { text: visitorStatusText.waitingForChat, value: visitorStatus.waitingForChat },
  { text: visitorStatusText.chatting, value: visitorStatus.chatting },
  { text: visitorStatusText.preChat, value: visitorStatus.preChat },
  { text: visitorStatusText.invited, value: visitorStatus.invited },
  { text: visitorStatusText.autoInvited, value: visitorStatus.autoInvited },
  { text: visitorStatusText.offlineMessage, value: visitorStatus.offlineMessage },
  { text: visitorStatusText.refusedByAgent, value: visitorStatus.refusedByOperator },
  { text: visitorStatusText.refusedByVisitor, value: visitorStatus.refusedByVisitor },
  { text: visitorStatusText.chatEnded, value: visitorStatus.chatEnded },
  { text: visitorStatusText.inSite, value: visitorStatus.inSite },
  { text: visitorStatusText.outOfSite, value: visitorStatus.outOfSite },
  { text: visitorStatusText.audioChatRequesting, value: visitorStatus.audioChatRequesting },
  { text: visitorStatusText.audioChatting, value: visitorStatus.audioChatting },
  { text: visitorStatusText.videoChatRequesting, value: visitorStatus.videoChatRequesting },
  { text: visitorStatusText.videoChatting, value: visitorStatus.videoChatting },
];
const clearSelect = () => {
  const element = document.querySelector('div[data-vmselect="open"]');
  if (element) {
    element.click();
  }
};
const clearSelectDebounce = _.debounce(clearSelect, 10);

const convertCustomFilter = (customFilter, customVariables) =>
  customFilter.map((item, idx) => {
    let cvType = 0;
    if (item.field === filterFieldType.customVariable) {
      const cv = _.find(customVariables, f => f.id === item.variableId);
      if (cv === null) return null;
      cvType = cv.type;
    }
    return {
      id: idx,
      field: item.field,
      op: item.op,
      values: item.values,
      variableId: item.variableId,
      variableType: cvType,
    };
  }).filter(item => item !== null);

const getAllConditions = (siteInfo, customVariables) => {
  let conditions = [];
  conditions.push({ name: filterFieldTypeText.status, field: filterFieldType.status, variableId: 0, variableType: 0, op: filterOperatorType.is });
  if (siteInfo && siteInfo.ifEnableSegmentation) {
    conditions.push({ name: filterFieldTypeText.visitorSegment, field: filterFieldType.visitorSegment, variableId: 0, variableType: 0, op: filterOperatorType.contains });
  }
  conditions.push({ name: filterFieldTypeText.agents, field: filterFieldType.agents, variableId: 0, variableType: 0, op: filterOperatorType.contains });
  if (siteInfo && siteInfo.ifDepartmentEnabled) {
    conditions.push({ name: filterFieldTypeText.department, field: filterFieldType.department, variableId: 0, variableType: 0, op: filterOperatorType.is });
  }
  conditions.push({ name: filterFieldTypeText.productService, field: filterFieldType.productService, variableId: 0, variableType: 0, op: filterOperatorType.is });
  conditions.push({ name: filterFieldTypeText.autoInvitation, field: filterFieldType.autoInvitation, variableId: 0, variableType: 0, op: filterOperatorType.is });
  conditions.push({ name: filterFieldTypeText.campaign, field: filterFieldType.campaign, variableId: 0, variableType: 0, op: filterOperatorType.is });
  conditions.push({ name: filterFieldTypeText.countryRegion, field: filterFieldType.countryRegion, variableId: 0, variableType: 0, op: filterOperatorType.is });
  conditions.push({ name: filterFieldTypeText.city, field: filterFieldType.city, variableId: 0, variableType: 0, op: filterOperatorType.is });
  conditions.push({ name: filterFieldTypeText.state, field: filterFieldType.state, variableId: 0, variableType: 0, op: filterOperatorType.is });

  if (siteInfo && siteInfo.ifEnableCustomVariables) {
    conditions = conditions.concat(customVariables.map((item) => {
      let op = filterOperatorType.is;
      if (item.type === customVariableType.plainText) {
        op = filterOperatorType.contains;
      }
      return { name: item.name, field: filterFieldType.customVariable, variableId: item.id, variableType: item.type, op };
    }));
  }
  return conditions;
};

class CustomFilterModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      customFilterConditions: convertCustomFilter(props.customFilter, props.customVariables),
    };
    this.maxId = this.state.customFilterConditions.length - 1;
    this.addFlag = false;
    const { siteInfo, campaigns, departments, autoInvitations, agents, customVariables, segments, currentAgentId } = props;

    this.allCodePlans = campaigns.map(item => ({ text: item.name, value: item.id }));
    this.allDepartments = departments.map(item => ({ text: item.name, value: item.id }));
    const currentAgent = [{ text: '@me', value: currentAgentId }];
    this.allOperators = currentAgent.concat(agents.map(item => ({ text: item.name, value: item.id })));
    this.allSegments = segments.map(item => ({ text: item.name, value: item.id }));
    this.allAutoInvitations = autoInvitations.map(item => ({ text: item.name, value: item.id }));
    const allConditions = getAllConditions(siteInfo, customVariables);
    this.allConditions = allConditions.map((item) => {
      switch (item.field) {
        case filterFieldType.status:
          return Object.assign({ values: [allStatus[0].value] }, item);
        case filterFieldType.campaign:
          return Object.assign({ values: [this.allCodePlans.length > 0 ? this.allCodePlans[0].value : -1] }, item);
        case filterFieldType.department:
          return Object.assign({ values: [this.allDepartments.length > 0 ? this.allDepartments[0].value : -1] }, item);
        case filterFieldType.agents:
          return Object.assign({ values: [this.allOperators.length > 0 ? this.allOperators[0].value : -1] }, item);
        case filterFieldType.visitorSegment:
          return Object.assign({ values: [this.allSegments.length > 0 ? this.allSegments[0].value : -1] }, item);
        case filterFieldType.autoInvitation:
          return Object.assign({ values: [this.allAutoInvitations.length > 0 ? this.allAutoInvitations[0].value : -1] }, item);
        default:
          return Object.assign({ values: [''] }, item);
      }
    });

    this.saveCustomFilters = this.saveCustomFilters.bind(this);
    this.addCondition = this.addCondition.bind(this);
    this.updateCondition = this.updateCondition.bind(this);
    this.updateOperation = this.updateOperation.bind(this);
    this.updateValue = this.updateValue.bind(this);
    this.deleteValue = this.deleteValue.bind(this);
    this.addOption = this.addOption.bind(this);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (!_.isEqual(this.state.customFilterConditions, nextState.customFilterConditions)) {
      return true;
    }
    return false;
  }
  componentDidUpdate() {
    const _this = this;
    setTimeout(() => {
      if (_this.container) {
        if (_this.addFlag) {
          _this.addFlag = false;
          const sTop = _this.container.scrollHeight - _this.container.clientHeight;
          if (sTop > 0) {
            _this.container.scrollTop = sTop;
          }
        } else if (_this.addOptionFlag) {
          _this.addOptionFlag = false;
          const sTop = _this.container.scrollHeight - _this.container.clientHeight;
          if (sTop > 0) {
            _this.container.scrollTop += 40;
          }
        }
      }
    }, 0);
  }
  saveCustomFilters() {
    this.props.saveCustomFilters(this.state.customFilterConditions.map(item => ({
      field: item.field,
      op: item.op,
      values: item.values,
      variableId: item.variableId,
    })));
  }
  addCondition() {
    this.maxId = this.maxId + 1;
    const c = this.allConditions[0];
    const newState = this.state.customFilterConditions.concat([{ id: this.maxId, ...c }]);
    this.addFlag = true;
    this.setState({ customFilterConditions: newState });
  }
  updateCondition(conditionId, value) {
    const tempV = _.find(this.state.customFilterConditions, item => item.id === conditionId);
    const c = _.find(this.allConditions, item => item.field + item.variableId === value);
    if (c) {
      const newFilterCondition = Object.assign({}, tempV, c);
      const newState = this.state.customFilterConditions.map((item) => {
        if (item.id === conditionId) {
          return newFilterCondition;
        }
        return item;
      });
      this.setState({ customFilterConditions: newState });
    }
  }
  updateOperation(conditionId, value) {
    const tempV = _.find(this.state.customFilterConditions, item => item.id === conditionId);
    const newFilterCondition = Object.assign({}, tempV, { op: value });
    const newState = this.state.customFilterConditions.map((item) => {
      if (item.id === conditionId) {
        return newFilterCondition;
      }
      return item;
    });
    this.setState({ customFilterConditions: newState });
  }
  updateValue(conditionId, valueId, newValue) {
    const tempV = _.find(this.state.customFilterConditions, item => item.id === conditionId);
    const newV = tempV.values.map((item, idx) => {
      if (idx === valueId) return newValue;
      return item;
    });
    const newFilterCondition = Object.assign({}, tempV, { values: newV });
    const newState = this.state.customFilterConditions.map((item) => {
      if (item.id === conditionId) {
        return newFilterCondition;
      }
      return item;
    });
    this.setState({ customFilterConditions: newState });
  }
  deleteValue(conditionId, valueId) {
    const oldV = _.find(this.state.customFilterConditions, item => item.id === conditionId);
    const newValues = oldV.values.filter((v, idx) => idx !== valueId);
    let newState;
    if (newValues.length > 0) {
      const newFilterCondition = Object.assign({}, oldV, { values: newValues });
      newState = this.state.customFilterConditions.map((item) => {
        if (item.id === conditionId) {
          return newFilterCondition;
        }
        return item;
      });
    } else {
      newState = this.state.customFilterConditions.map((item) => {
        if (item.id === conditionId) return null;
        return item;
      }).filter(item => item !== null);
    }
    this.setState({ customFilterConditions: newState });
  }
  addOption(conditionId) {
    const tempV = _.find(this.state.customFilterConditions, item => item.id === conditionId);
    const c = _.find(this.allConditions, item => item.field === tempV.field);
    if (c) {
      const newV = tempV.values.concat(c.values);
      const newFilterCondition = Object.assign({}, tempV, { values: newV });
      const newState = this.state.customFilterConditions.map((item) => {
        if (item.id === conditionId) return newFilterCondition;
        return item;
      });
      this.addOptionFlag = true;
      this.setState({ customFilterConditions: newState });
    }
  }
  render() {
    const allConditions = this.allConditions;
    const allCodePlans = this.allCodePlans;
    const allDepartments = this.allDepartments;
    const allOperators = this.allOperators;
    const allSegments = this.allSegments;
    const allAutoInvitations = this.allAutoInvitations;
    return (<Modal
      closeModal={this.props.closeModal}
      className={style.customFilter}
      title="Custom Filter"
      actions={{
        okProps: { text: 'OK', eventClick: this.saveCustomFilters },
        cancelProps: { text: 'Cancel' },
      }}
    >
      <div className={style.containerWrapper}>
        <div
          className={style.container}
          ref={(element) => { this.container = element; }}
          onScroll={() => {
            clearSelectDebounce();
          }}
        >
          {
            this.state.customFilterConditions.map((fieldCondition, idx) => {
              let valueOptions = [];
              let isDDLType = false;
              let operationType = 0;
              switch (fieldCondition.field) {
                case filterFieldType.status:
                  valueOptions = allStatus;
                  isDDLType = true;
                  operationType = 0;
                  break;
                case filterFieldType.campaign:
                  valueOptions = allCodePlans;
                  isDDLType = true;
                  operationType = 0;
                  break;
                case filterFieldType.department:
                  valueOptions = allDepartments;
                  isDDLType = true;
                  operationType = 0;
                  break;
                case filterFieldType.agents:
                  valueOptions = allOperators;
                  isDDLType = true;
                  operationType = 1;
                  break;
                case filterFieldType.visitorSegment:
                  valueOptions = allSegments;
                  isDDLType = true;
                  operationType = 1;
                  break;
                case filterFieldType.autoInvitation:
                  valueOptions = allAutoInvitations;
                  isDDLType = true;
                  operationType = 0;
                  break;
                case filterFieldType.countryRegion:
                case filterFieldType.city:
                case filterFieldType.state:
                case filterFieldType.productService:
                  isDDLType = false;
                  operationType = 2;
                  break;
                case filterFieldType.customVariable:
                  if (fieldCondition.variableType === customVariableType.notDefined) {
                    operationType = 5;
                  } else if (fieldCondition.variableType === customVariableType.integer || fieldCondition.variableType === customVariableType.decimal) {
                    operationType = 4;
                  } else {
                    operationType = 3;
                  }
                  isDDLType = false;
                  break;
                default:
                  isDDLType = false;
                  operationType = 0;
                  break;
              }
              let isDDLOperation = true;
              let operationText = '';
              let operationOptions = [];
              let operationInputValue = fieldCondition.op;
              switch (operationType) {
                case 0:
                  isDDLOperation = false;
                  operationText = 'is';
                  operationInputValue = filterOperatorType.is;
                  break;
                case 1:
                  isDDLOperation = false;
                  operationText = 'contains';
                  operationInputValue = filterOperatorType.contains;
                  break;
                case 2:
                  operationOptions = [{ text: 'is', value: filterOperatorType.is }, { text: 'is not', value: filterOperatorType.isNot }];
                  if (!operationInputValue) {
                    operationInputValue = filterOperatorType.is;
                  }
                  break;
                case 3:
                  operationOptions = [{ text: 'contains', value: filterOperatorType.contains }, { text: 'not contains', value: filterOperatorType.notContains }];
                  if (!operationInputValue) {
                    operationInputValue = filterOperatorType.contains;
                  }
                  break;
                case 4:
                  operationOptions = [{ text: 'is', value: filterOperatorType.is }, { text: 'is less than', value: filterOperatorType.isLessThan }, { text: 'is more than', value: filterOperatorType.isMoreThan }];
                  if (!operationInputValue) {
                    operationInputValue = filterOperatorType.is;
                  }
                  break;
                case 5:
                  operationOptions = [{ text: 'is', value: filterOperatorType.is }, { text: 'is not', value: filterOperatorType.isNot },
                  { text: 'contains', value: filterOperatorType.contains }, { text: 'not contains', value: filterOperatorType.notContains },
                  { text: 'is less than', value: filterOperatorType.isLessThan }, { text: 'is more than', value: filterOperatorType.isMoreThan }];
                  if (!operationInputValue) {
                    operationInputValue = filterOperatorType.is;
                  }
                  break;
              }

              return (
                <FieldCondition
                  key={idx}
                  id={fieldCondition.id}
                  conditionOptions={allConditions.map(item => ({ text: item.name, value: item.field + item.variableId }))}
                  conditionInput={{ value: fieldCondition.field + fieldCondition.variableId, onChange: this.updateCondition }}
                  operationOptions={operationOptions}
                  operationInput={{ value: operationInputValue, onChange: this.updateOperation }}
                  operationText={operationText}
                  isDDLOperation={isDDLOperation}
                  isDDLType={isDDLType}
                  valueOptions={valueOptions}
                  values={fieldCondition.values}
                  onValueChange={this.updateValue}
                  onDelete={this.deleteValue}
                  addOption={this.addOption}
                />);
            })
          }
        </div>
      </div>
      <div className={style.addCondition}>
        <Link onClick={this.addCondition}>
          <span>Add Condition</span>
        </Link>
      </div>
    </Modal>);
  }
}

CustomFilterModal.propTypes = {
  siteInfo: React.PropTypes.object,
  campaigns: React.PropTypes.array,
  departments: React.PropTypes.array,
  autoInvitations: React.PropTypes.array,
  agents: React.PropTypes.array,
  customVariables: React.PropTypes.array,
  segments: React.PropTypes.array,
  customFilter: React.PropTypes.array,
  ifOpen: React.PropTypes.bool,
  closeModal: React.PropTypes.func,
  saveCustomFilters: React.PropTypes.func,
};

export default CustomFilterModal;



