import React from 'react';
import classnames from 'classnames';
import style from './Input.css';

class Input extends React.Component {
  static propTypes = {
    isReadOnly: React.PropTypes.bool,
    isDisabled: React.PropTypes.bool,
    placeholder: React.PropTypes.string,
    type: React.PropTypes.string,
    autoComplete: React.PropTypes.bool,
    className: React.PropTypes.string,
    maxLength: React.PropTypes.number,
    input: React.PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
    meta: React.PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
    inputRef: React.PropTypes.func,
  }

  static defaultProps = {
    type: 'text',
    autoComplete: false,
  }

  static examples = {

  }

  // componentDidMount() {
  //   if (this.props.focusOnMount) {
  //     this.input.focus();
  //   }
  // }

  render() {
    const { isDisabled, placeholder, input, type, autoComplete, maxLength, className, meta, isReadOnly, inputRef, ...rest } = this.props;
    return (
      <input
        {...input}
        readOnly = {isReadOnly ? 'readOnly' : false}
        disabled={isDisabled ? 'disabled' : false}
        className={classnames(
          className,
          style.input,
          meta.touched && meta.invalid && style.error
        )}
        type={type}
        placeholder={placeholder}
        autoComplete={autoComplete ? 'on' : 'off'}
        maxLength={maxLength}
        ref={inputRef}
        {...rest}
      />
    );
  }
}

export default Input;



