import React from 'react';
import style from './CustomFilter.css';
import Select from '../../Select/Select';
import Link from '../../Link/Link';
import Input from '../../Input/Input';
import Icon from '../../Icon/Icon';
import { icons } from '../../../constants/enums';

class FieldCondition extends React.Component {
  render() {
    const { id, conditionOptions, conditionInput, operationOptions, operationInput, operationText, isDDLOperation, isDDLType, values, valueOptions } = this.props;
    const w = isDDLOperation ? 132 : (operationText == 'is' ? 31 : 74);
    return (<div className={style.fieldConditionWrapper}>
      <div className={style.fieldCondition}>
        <Select
          options={conditionOptions}
          input={{ value: conditionInput.value, onChange: (value) => { conditionInput.onChange(id, value); } }}
          width={155}
        />
      </div>
      <div className={style.conditionOptionsWrapper}>
        {
          isDDLType && (
            values.map((item, idx) => {
              const canRemove = idx !== 0;
              return (<div
                key={idx}
                className={style.conditionValueWrapper}
              >
                <div className={style.conditionOptions} style={{ width: w }}>
                  {!canRemove && isDDLOperation && (
                    <Select
                      options={operationOptions}
                      input={{ value: operationInput.value, onChange: (value) => { operationInput.onChange(id, value); } }}
                      width={110}
                    />) }
                  { !canRemove && !isDDLOperation && (
                    <span className={style.operationText}>{operationText}</span>
                  )}
                  { canRemove && (
                  <span className={style.operationText}>or</span>
                    )}
                </div>
                <div className={style.conditionValue}>
                  <Select
                    options={valueOptions}
                    input={{ value: item, onChange: (newValue) => { this.props.onValueChange(id, idx, newValue); } }}
                    width={200}
                  />
                  <Icon className={style.closeIcon} type={icons.close} color="#666" size={10} onClick={() => { this.props.onDelete(id, idx); }} />
                </div>
              </div>);
            })
          )
        }
        {
          !isDDLType && (
            values.map((item, idx) => {
              const canRemove = idx !== 0;
              return (<div
                key={idx}
                className={style.conditionValueWrapper}
              >
                <div className={style.conditionOptions} style={{ width: w }}>
                  {!canRemove && isDDLOperation && (
                    <Select
                      options={operationOptions}
                      input={{ value: operationInput.value, onChange: (value) => { operationInput.onChange(id, value); } }}
                      width={110}
                    />) }
                  { !canRemove && !isDDLOperation && (
                    <span className={style.operationText}>{operationText}</span>
                  )}
                  { canRemove && (
                  <span className={style.operationText}>or</span>
                    )}
                </div>
                <div className={style.conditionValue}>
                  <Input
                    input={{ value: item, onChange: (e) => { this.props.onValueChange(id, idx, e.target.value); } }}
                    className={style.input}
                    width={200}
                    meta={{}}
                  />
                  <Icon className={style.closeIcon} type={icons.close} color="#666" size={10} onClick={() => { this.props.onDelete(id, idx); }} />
                </div>
              </div>);
            })
          )
        }
        <div className={style.addOption} style={{ marginLeft: w }}>
          <Link onClick={() => { this.props.addOption(this.props.id); }}>
            <span>Add Option</span>
          </Link>
        </div>
      </div>
    </div>);
  }
}

FieldCondition.propTypes = {
  id: React.PropTypes.number,
  conditionOptions: React.PropTypes.array,
  conditionInput: React.PropTypes.shape({
    onChange: React.PropTypes.func.isRequired,
    value: React.PropTypes.number.isRequired,
  }).isRequired,
  operationOptions: React.PropTypes.array,
  operationInput: React.PropTypes.shape({
    onChange: React.PropTypes.func.isRequired,
    value: React.PropTypes.number.isRequired,
  }).isRequired,
  operationText: React.PropTypes.string,
  isDDLOperation: React.PropTypes.bool,
  isDDLType: React.PropTypes.bool,
  valueOptions: React.PropTypes.array,
  values: React.PropTypes.array,
  onValueChange: React.PropTypes.func,
  onDelete: React.PropTypes.func,
  addOption: React.PropTypes.func,
};

export default FieldCondition;



