import React from 'react';
import * as _ from 'lodash';
import { findDOMNode } from 'react-dom';
import scrollIntoView from './scrollIntoView';
import Option from './Option';
import OptGroup from './OptGroup';
import style from './Select.css';
import { createGUID } from '../../utils/guid';

function staticSelectGenerator({ options, selectedValue, onChange }, gid) {
  const optionGroups = options.reduce((_obj, option, idx) => {
    const obj = _obj;
    const group = option.group || '';
    const text = option.text || option.value;
    const component = <Option value={option.value} id={gid} key={`${gid}_${idx}`} selectedValue={selectedValue} text={text} onChange={onChange} />;
    if (obj[group]) obj[group].push(component);
    else obj[group] = [component];
    return obj;
  }, {});
  let optionComponents = [];
  _.forEach(Object.keys(optionGroups), (key) => {
    if (key === '') {
      optionComponents = optionComponents.concat(optionGroups[key]);
    } else {
      optionComponents.push(<OptGroup label={key}>
        {optionGroups[key]}
      </OptGroup>);
    }
  });
  return optionComponents;
}
class DropdownMenu extends React.Component {
  constructor(props) {
    super(props);
    this.gid = createGUID();
  }
  componentDidMount() {
    const props = this.props;
    if (props.visible) {
      this.scrollActiveItemToView();
    }
  }
  shouldComponentUpdate(nextProps) {
    if (this.props.selectedValue !== nextProps.selectedValue
     || this.props.visible !== nextProps.visible
     || !_.isEqual(this.props.options, nextProps.options)) {
      return true;
    }
    return false;
  }

  componentDidUpdate() {
    const props = this.props;
    if (props.visible) {
      this.scrollActiveItemToView();
    }
  }

  scrollActiveItemToView() {
    // scroll into view
    const activeItem = document.getElementById(this.gid);
    if (activeItem) {
      scrollIntoView(activeItem, findDOMNode(this.refs.menuWrapper), {
        onlyScrollIfNeeded: true,
        alignWithTop: true,
      });
    }
  }

  render() {
    if (this.props.options && this.props.options.length) {
      return (
        <div
          ref="menuWrapper"
          className={this.props.className}
          style={{ overflow: 'auto' }}
          onFocus={this.props.onPopupFocus}
          onMouseDown={(event) => {
            event.preventDefault();
          }}
        >
          <ul
            key={this.gid}
            className={style['dropdown-menu']}
          >
            {staticSelectGenerator(this.props, this.gid)}
          </ul>
        </div>
      );
    }
    return null;
  }
}

DropdownMenu.propTypes = {
  className: React.PropTypes.string,
  onChange: React.PropTypes.func,
  options: React.PropTypes.array.isRequired,
  selectedValue: React.PropTypes.oneOfType([React.PropTypes.number, React.PropTypes.string]).isRequired,
  visible: React.PropTypes.bool,
};

export default DropdownMenu;



